export default {
	data() {
		return {
			
		}
	},
	onNavigationBarButtonTap(event){
		uni.downloadFile({
			url: 'https://www.example.com/file/test', //仅为示例，并非真实的资源
			success: (res) => {
				if (res.statusCode === 200) {
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function () {
							uni.showToast({
								title:"保存成功！",
								icon:"none"
							})
						}
					});
				}
			}
		});
	},
	methods: {
		
	}
}
